.c-cursor__stalker {
  /*--最初透明にして置かないと、ページ遷移時に左上隅にカーソルが毎回見えてしまい不恰好！
  JSでマウスが動いた時にopacityを付与し表示する --*/
  opacity: 0;
}
@media screen and (min-width: 1024px) {
  .c-cursor,
.c-cursor__stalker {
    position: fixed;
    pointer-events: none;
    transition: transform 0.3s ease;
  }
}

@media screen and (min-width: 1024px) {
  .c-cursor {
