    z-index: 300;
    margin: -0.3125rem -0.3125rem;
    /* --- aタグ、buttonタグにホバー時にjsでクラス付与 --- */
  }
  .c-cursor.is-active {
    transform: scale(0);
    opacity: 0;
    transition: 0.5s linear -0.2s;
  }
}

@media screen and (min-width: 1024px) {
  .c-cursor__stalker {
    width: 2.5rem;
    height: 2.5rem;
    border-radius: 50%;
    border: 2px solid #F9F871;
    z-index: 299;
    margin: -1.25rem -1.25rem;
    /* --- aタグ、buttonタグにホバー時にjsでクラス付与 --- */
  }
  .c-cursor__stalker.is-active {
    transform: scale(3.5);
    border-width: 1px;
    opacity: 0.5;
    transition: 0.5s linear -0.5s;
  }
}

