/* --- ローディング画面 最初の1回だけアニメーションをする使用(jsで制御) --- */
.p-loading {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 9999;
  /* --- ローディング時にのトップページが見えないようにする --- */
  opacity: 1;
}
.p-loading::before, .p-loading::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 50%;
  background-color: #111111;
}
.p-loading::before {
  top: 0;
  left: 0;
}
.p-loading::after {
  bottom: 0;
  left: 0;
}

/* --- 2回目以降のアクセスで非表示 --- */
.p-loading.is-hidden {
  opacity: 0;
  visibility: hidden;
}

