  opacity: 0;
  position: absolute;
  z-index: 2;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to right top, transparent 50%, #F9F871 50%, #F9F871 calc(50% + 3px), transparent calc(50% - 3px));
}

/* ローディングアニメーション
  ===================================================*/
.p-loading.is-active {
  opacity: 1;
  visibility: hidden;
  transition: visibility 0s ease 3.1s, opacity 0s;
}
.p-loading.is-active::before, .p-loading.is-active::after {
  transform: scale(1, 0);
  transition: transform 1.7s cubic-bezier(0.9, 0.1, 0.3, 0.9) 1.8s, -webkit-transform 1.2s cubic-bezier(0.9, 0.1, 0.3, 0.9) 1.8s;
}
.p-loading.is-active::before {
  transform-origin: left top;
}
.p-loading.is-active::after {
  transform-origin: left bottom;
}
.p-loading.is-active .p-loading__logo {
  animation: loading-logo-opacity 2.25s ease-in both;
}
.p-loading.is-active .p-loading__copy {
  animation: loading-logo-opacity 1.65s ease-in 0.5s both;
}
@keyframes loading-logo-opacity {
  0%, 1%, 100% {
    opacity: 0;
  }
  10%, 90% {
    opacity: 1;
  }
}
.p-loading.is-active .p-loading__line {
  animation: loading-line-scale 1.5s cubic-bezier(0.9, 0.1, 0.3, 0.9), loading-line-opacity 0.5s ease 1.9s both;
}

@keyframes loading-line-scale {
  0% {
    width: 0;
    height: 0;
  }
  100% {
    width: 100%;
    height: 100%;
  }
}
@keyframes loading-line-opacity {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
