.p-topMv::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: block;
  background-color: rgba(17, 17, 17, 0.6);
  z-index: 200;
}

/* --- 1回目のローディング時のみアニメーション --- */
.p-topMv.is-active::before {
  opacity: 0;
  visibility: hidden;
  transition: opacity 1s ease 3.1s, visibility 1s ease 3.1s;
}

/* --- 2回目以降のローディングでは非表示 --- */
.p-topMv.is-hidden::before {
  visibility: hidden;
}

.p-topMv__title {
  position: absolute;
  max-width: 600px;
  width: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  z-index: 2;
  color: #FFFFFF;
  text-shadow: 1px 2px 7px #333333;
  clip-path: inset(0 100% 0 0);
}
@media screen and (min-width: 768px) {
  .p-topMv__title {
    max-width: none;
  }
}

/* --- 1回目のローディング時のみアニメーション --- */
.p-topMv.is-active .p-topMv__title {
  animation: clip-to-right 1s linear 3s both;
}

/* --- 2回目以降のローディングでは非表示 --- */
.p-topMv.is-hidden .p-topMv__title {
  clip-path: initial;
}

@keyframes clip-to-right {
  0% {
    -webkit-clip-path: inset(0 100% 0 0);
    clip-path: inset(0 100% 0 0);
  }
  to {
    -webkit-clip-path: inset(0% 0% 0% 0%);
    clip-path: inset(0% 0% 0% 0%);
  }
}
