  opacity: 1;
  transform: translate(-50%, 0);
  transition: opacity 1s ease-in 4s, transform 1s ease 4s;
}
@media screen and (min-width: 768px) {
  .p-topMv.is-active .p-topMV__newPost {
    transform: translate(0);
  }
}

/* ---  *2回目のローディング以降は delay無しで下からフェイドイン開始 --- */
